import { Step } from '../types';
import i18n from 'i18next';

// Define step IDs
const stepIDs = [
  'httpRequest',
  'domTree',
  'styleProcessing',
  'renderTree',
  'layout',
  'paint',
  'composite'
];

// Define step colors
const stepColors = [
  'blue',
  'green',
  'purple',
  'orange',
  'red',
  'yellow',
  'teal'
];

// Create steps with translations
export const steps: Step[] = stepIDs.map((id, index) => ({
  id,
  title: i18n.t(`steps.${index}.title`),
  description: i18n.t(`steps.${index}.description`),
  detailedText: i18n.t(`steps.${index}.detailedText`),
  color: stepColors[index]
}));

export const totalSteps = steps.length;

// Helper function to get step by ID - with fresh translations
export const getStepById = (id: string): Step | undefined => {
  const index = stepIDs.findIndex(stepId => stepId === id);
  if (index === -1) return undefined;
  
  return {
    id,
    title: i18n.t(`steps.${index}.title`),
    description: i18n.t(`steps.${index}.description`),
    detailedText: i18n.t(`steps.${index}.detailedText`),
    color: stepColors[index]
  };
};

// Helper function to get step by index - with fresh translations
export const getStepByIndex = (index: number): Step | undefined => {
  if (index < 0 || index >= totalSteps) return undefined;
  
  const id = stepIDs[index];
  return {
    id,
    title: i18n.t(`steps.${index}.title`),
    description: i18n.t(`steps.${index}.description`),
    detailedText: i18n.t(`steps.${index}.detailedText`),
    color: stepColors[index]
  };
};

// Function to refresh steps with current translations
export const refreshSteps = (): Step[] => {
  return stepIDs.map((id, index) => ({
    id,
    title: i18n.t(`steps.${index}.title`),
    description: i18n.t(`steps.${index}.description`),
    detailedText: i18n.t(`steps.${index}.detailedText`),
    color: stepColors[index]
  }));
};

// Listen for language changes to trigger step refresh
i18n.on('languageChanged', () => {
  // Update the steps array with new translations
  Object.assign(steps, refreshSteps());
  
  // Notify components that steps have been updated
  window.dispatchEvent(new Event('stepsUpdated'));
});