import React, { lazy, Suspense } from 'react';
import { steps } from '../../constants/steps';

// Lazy load all animation components
const HttpRequestAnimation = lazy(() => import('./HttpRequestAnimation'));
const DomTreeAnimation = lazy(() => import('./DomTreeAnimation'));
const StyleProcessingAnimation = lazy(() => import('./StyleProcessingAnimation'));
const RenderTreeAnimation = lazy(() => import('./RenderTreeAnimation'));
const LayoutAnimation = lazy(() => import('./LayoutAnimation'));
const PaintAnimation = lazy(() => import('./PaintAnimation'));
const CompositeAnimation = lazy(() => import('./CompositeAnimation'));

interface AnimationProps {
  step: number;
  isVisible: boolean;
}

const AnimationComponent: React.FC<AnimationProps> = ({ step, isVisible }) => {
  return (
    <Suspense fallback={<div className="text-gray-500">Loading animation...</div>}>
      {(() => {
        switch (steps[step].id) {
          case 'httpRequest':
            return <HttpRequestAnimation isVisible={isVisible} />;
          case 'domTree':
            return <DomTreeAnimation isVisible={isVisible} />;
          case 'styleProcessing':
            return <StyleProcessingAnimation isVisible={isVisible} />;
          case 'renderTree':
            return <RenderTreeAnimation isVisible={isVisible} />;
          case 'layout':
            return <LayoutAnimation isVisible={isVisible} />;
          case 'paint':
            return <PaintAnimation isVisible={isVisible} />;
          case 'composite':
            return <CompositeAnimation isVisible={isVisible} />;
          default:
            return null;
        }
      })()}
    </Suspense>
  );
};

export default AnimationComponent;
