import React, { useState, useEffect } from 'react';
import { AnimatePresence, motion } from 'framer-motion';
import { useTranslation } from 'react-i18next';
import './i18n';
import LanguagePicker from './components/LanguagePicker';
import { useNavigate, useLocation, Routes, Route, Navigate } from 'react-router-dom';
import { steps, totalSteps } from './constants/steps';
import { fadeVariants } from './constants/animations';
import AnimationComponent from './components/animations/AnimationComponent';
import { APP_BASE_PATH, buildPath } from './config';

const MainContent = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { t, i18n } = useTranslation();
  
  // 获取当前步骤 ID
  const getCurrentStepId = () => {
    // 从路径中提取最后一部分作为步骤 ID
    const pathParts = location.pathname.split('/');
    return pathParts[pathParts.length - 1] || 'httpRequest';
  };
  
  const [currentStep, setCurrentStep] = useState(() => {
    const stepId = getCurrentStepId();
    const index = steps.findIndex(step => step.id === stepId);
    return index >= 0 ? index : 0;
  });

  // 当 URL 变化时更新当前步骤
  useEffect(() => {
    const stepId = getCurrentStepId();
    const index = steps.findIndex(step => step.id === stepId);
    if (index >= 0) {
      setCurrentStep(index);
    }
  }, [location.pathname]);

  // 导航到指定步骤 - 使用带前缀的完整路径
  const navigateToStep = (index: number) => {
    if (index >= 0 && index < totalSteps) {
      // 使用 buildPath 辅助函数生成带前缀的完整路径
      navigate(buildPath(steps[index].id));
    }
  };

  const handleNext = () => {
    if (currentStep < totalSteps - 1) {
      navigateToStep(currentStep + 1);
    }
  };

  const handlePrev = () => {
    if (currentStep > 0) {
      navigateToStep(currentStep - 1);
    }
  };

  const handleStepSelect = (index: number) => {
    navigateToStep(index);
  };

  // 監聽語言變更事件
  useEffect(() => {
    const handleLanguageChange = () => {
      // 強制更新組件
      forceUpdate({});
    };

    window.addEventListener('languageChanged', handleLanguageChange);
    return () => {
      window.removeEventListener('languageChanged', handleLanguageChange);
    };
  }, []);

  const [, forceUpdate] = useState({});

  return (
    <div className="min-h-screen bg-gray-100">
      {/* Header */}
      <header className="bg-blue-50 py-6 px-4 shadow-sm">
        <div className="max-w-7xl mx-auto flex justify-between items-center flex-wrap gap-4">
          <h1 className="pl-1 text-3xl font-bold text-gray-800">
            {t('title')}
          </h1>
          <LanguagePicker />
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 py-8">
        {/* Step Indicators */}
        <div className="flex mb-8 flex-col lg:flex-row justify-content-center gap-2">
          {steps.map((step, index) => (
            <button
              key={step.id}
              onClick={() => handleStepSelect(index)}
              className={`flex-1 py-2 px-4 rounded-lg transition-colors ${
                index === currentStep
                  ? 'bg-blue-500 text-white'
                  : 'bg-white text-gray-600 hover:bg-blue-50'
              }`}
            >
              {step.title}
            </button>
          ))}
        </div>

        {/* Content Area */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          {/* Description Panel */}
          <AnimatePresence mode="wait">
            <motion.div
              key={currentStep}
              initial="hidden"
              animate="visible"
              exit="exit"
              variants={fadeVariants}
              className="bg-white rounded-xl p-6 shadow-sm"
            >
              <h2 className="text-2xl font-bold text-gray-800 mb-4">
                {steps[currentStep].title}
              </h2>
              <p className="text-gray-600 mb-4">{steps[currentStep].description}</p>
              <div className="bg-blue-50 rounded-lg p-4">
                <pre className="whitespace-pre-wrap text-gray-700 font-mono text-sm">
                  {steps[currentStep].detailedText}
                </pre>
              </div>
            </motion.div>
          </AnimatePresence>

          {/* Animation Panel */}
          <AnimatePresence mode="wait">
            <motion.div
              key={currentStep}
              initial="hidden"
              animate="visible"
              exit="exit"
              variants={fadeVariants}
              className="bg-white rounded-xl py-6 shadow-sm min-h-[400px] flex items-center justify-center"
            >
              <AnimationComponent step={currentStep} isVisible={true} />
            </motion.div>
          </AnimatePresence>
        </div>

        {/* Navigation Controls */}
        <div className="flex justify-between mt-8">
          <button
            onClick={handlePrev}
            disabled={currentStep === 0}
            className={`px-6 py-2 rounded-lg transition-colors ${
              currentStep === 0
                ? 'bg-gray-200 text-gray-400 cursor-not-allowed'
                : 'bg-blue-500 text-white hover:bg-blue-600'
            }`}
          >
            {t('previous')}
          </button>
          <button
            onClick={handleNext}
            disabled={currentStep === totalSteps - 1}
            className={`px-6 py-2 rounded-lg transition-colors ${
              currentStep === totalSteps - 1
                ? 'bg-gray-200 text-gray-400 cursor-not-allowed'
                : 'bg-blue-500 text-white hover:bg-blue-600'
            }`}
          >
            {t('next')}
          </button>
        </div>
      </main>
    </div>
  );
};

const App: React.FC = () => {
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    // 检查当前路径是否是应用的根路径
    if (location.pathname === '/' || location.pathname === APP_BASE_PATH || location.pathname === `${APP_BASE_PATH}/`) {
      // 重定向到第一个步骤，使用带前缀的完整路径
      navigate(buildPath('httpRequest'), { replace: true });
    }
  }, [location.pathname, navigate]);

  return (
    <React.Suspense fallback={
      <div className="min-h-screen flex items-center justify-center bg-gray-100">
        <div className="text-xl text-gray-600">Loading...</div>
      </div>
    }>
      <Routes>
      {/* 匹配应用的根路径 */}
      <Route path="/" element={<Navigate to={buildPath('httpRequest')} replace />} />
      <Route path={APP_BASE_PATH} element={<Navigate to={buildPath('httpRequest')} replace />} />
      <Route path={`${APP_BASE_PATH}/`} element={<Navigate to={buildPath('httpRequest')} replace />} />
      
      {/* 匹配步骤路径 */}
      <Route path={`${APP_BASE_PATH}/:stepId`} element={<MainContent />} />
      
      {/* 捕获所有其他路径 */}
      <Route path="*" element={<Navigate to={buildPath('httpRequest')} replace />} />
      </Routes>
    </React.Suspense>
  );
};

export default App;
